.sel1,
option {
  color: #fff;
  border-color: #383848;
  cursor: pointer;
}
.cont1 {
  max-width: 1000px;
  min-height: 180px;
  padding: 20px;
  height: 100%;
  background-color: #1b1b28;
  border-radius: 10px;
}
.inp1,
.sel1,
option {
  transition: 0.3s;
  text-align-last: center;
}
.trans_cont {
  background-color: #272738;
  border-radius: 5px;
  padding: 10px;
}
.main_cont {
  background-color: #1b1b28;
  padding: 30px;
  border-radius: 5px;
  border-width: 1px;
  border-color: rgb(255, 255, 255, 0.3);
  border-style: solid;
}
.cont_s {
  background-color: #272738;
  display: flex;
  border-radius: 10px;
}
.sel1,
.sel1:focus,
option {
  background-color: #383848;
}
.sel1 {
  background-image: linear-gradient(45deg, transparent 50%, #fff 50%),
    linear-gradient(135deg, #fff 50%, transparent 50%);
  background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), 100% 0;
  background-size: 5px 5px, 5px 5px, 2.5em 2.5em;
  background-repeat: no-repeat;
}
option {
  height: 30px;
}
.sel1:hover {
  border-color: #7b98f9;
}
.table1 td,
.table1 th {
  background-color: #272738;
  text-align: center;
  vertical-align: middle;
}
.inp1,
.inp1:focus {
  background-color: #383848;
  color: #fff;
}
.table1 {
  border-radius: 5px;
  padding: 0;
}
.inp1 {
  border-color: #383848;
}
.inp1:hover {
  border-color: #7b98f9;
}
